body{
    background-color: #091824;
    z-index: -1;
}

.customText_w{
    font-family: 'Maven Pro', 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    word-wrap: break-word;
    font-size: 3vh;
    color: white;
}

.customText_w_big{
    font-family: 'Maven Pro', 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    word-wrap: break-word;
    font-size: 3.5vh;
    color: white;
}

.customText_b{
    font-family: 'Maven Pro', 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    word-wrap: break-word;
    font-size: 3vh;
    color: black;
}

.Margin_1{
    position: absolute;
    left: 1vh;
    right: 1vh;
    top: 1vh;
    bottom: 1vh;
}

.Margin_2{
    position: absolute;
    left: 2vh;
    right: 2vh;
    top: 2vh;
    bottom: 2vh;
}

.Margin_3{
    position: absolute;
    left: 3vh;
    right: 3vh;
    top: 3vh;
    bottom: 3vh;
}

.std_BTN{
    background: -webkit-linear-gradient(#506371, #374855);
    background: linear-gradient(#506371, #374855);
} 

.dark_BTN{
    background-image: -webkit-linear-gradient(#142533, #091824);
    background-image: linear-gradient(#142533, #091824); 
}
.PageBorder{
    background-image: -webkit-linear-gradient(#21303C, #142533);
    background-image: linear-gradient(#21303C, #142533);
    position: absolute;
    left: 2vh;
    right: 2vh;
    top: 2vh;
    bottom: 2vh;
}

.MoveTop{
    margin-top: 2vh;
}
.Home_BTN{
    width: 100%;
    height: 90%;
}

.Home_BtnBox{
    position: absolute;
    top: 10vh;
    width: 100%;
    bottom: 0;
}

.Home_TitleBox{
    position: absolute;
    width: 100%;
    height: 9vh;
    background-image: -webkit-linear-gradient(#506371, #374855);
    background-image: linear-gradient(#506371, #374855);
    border-style: solid;
    border-color: #091824; 
}

.Home_BTN_Row{
    height: 50%;
}

.Home_InfoText{
    width: 90%;
    left: 5%;
    position: relative;
    margin-top: 2vh;
}
.Admin_BTN{
    width: 90%;
    height: 90%;
    margin: 5%;
}

.Admin_ContentBox{
    position: absolute;
    top: 10vh;
    width: 100%;
    bottom: 5vh;
}

.Admin_TitleBox{
    position: absolute;
    width: 100%;
    height: 9vh;
    background-image: -webkit-linear-gradient(#142533, #091824);
    background-image: linear-gradient(#142533, #091824);
    border-style: solid;
    border-color: #091824; 
}

.Admin_PassForm{
    position: relative;
    width: 90%;
    left: 5%;
    height: 4vh;
}

.Admin_InfoText{
    width: 90%;
    left: 5%;
    position: relative;
    margin-top: 1vh;
}

.Admin_BTN_Row{
    width: 100%;
    height: 23vh;
}
